
*{margin:0;padding:0;list-style-type:none;
}
h3,.h3{font-size:16px;
}
.txtMarquee-top{ 
		width:100%;  
		overflow:hidden; 
		position:relative;  
		border:0px solid #ccc;  
		display: -webkit-box;  
		display: -ms-flexbox;  
		display: flex;
		-webkit-box-align: center;
		    -ms-flex-align: center;
		        align-items: center;
		-webkit-box-pack: center;
		    -ms-flex-pack: center;
		        justify-content: center;
}
.txtMarquee-top .bd{
		background:#afe5fe;
		width:80%;
		padding:0px;  
		height:30px;
		border-radius:5px;
}
.txtMarquee-top .winning-list-ul li{
		height:30px; 
		line-height:30px;
}



