
* {
	margin: 0;
	padding: 0;
	list-style-type: none;
}
html,
body,
div,
span,
a,
p,
img,
dl,
dt,
dd,
ol,
ul,
li {
	border: 0;
	margin: 0;
	padding: 0;
	font-size: 100%;
	text-decoration: none
}
img {
	color: transparent;
	font-size: 0;
	vertical-align: middle;
	-ms-interpolation-mode: bicubic;
	max-width: 100%
}
ol,
ul {
	list-style: none
}
li {
	display: list-item
}
:focus,
.focus {
	outline: none
}
