
.raider-calendar-dialog{
	  position: fixed;
	  top: 0;
	  left: 0;
	  width: 100%;
	  height: 100%;
	  background-color: rgba(0, 0, 0, 0.5);
	  display: -webkit-box;
	  display: -ms-flexbox;
	  display: flex;
	  display: -webkit-flex;
	  -webkit-box-pack: center;
	      -ms-flex-pack: center;
	          justify-content: center;
	  -webkit-justify-content: center;
	  -webkit-box-align: center;
	      -ms-flex-align: center;
	          align-items: center;
	  -webkit-align-items: center;
	  z-index: 100;
}
#total_outer_div{
				 margin-left:6%;
				 margin-right: 6%;
	
				 display: -webkit-box;
	
				 display: -ms-flexbox;
	
				 display: flex;
				 border-radius: 6px;
				 -webkit-box-orient: horizontal;
				 -webkit-box-direction: normal;
				     -ms-flex-direction: row;
				         flex-direction: row;
				 -webkit-box-align: center;
				     -ms-flex-align: center;
				         align-items: center;
				 -webkit-box-pack: center;
				     -ms-flex-pack: center;
				         justify-content: center;
				 background: white;
}
/*总层*/
#total_div {
                width: 100%;
               	display: -webkit-box;
               	display: -ms-flexbox;
               	display: flex;
				-webkit-box-orient: vertical;
				-webkit-box-direction: normal;
				    -ms-flex-direction: column;
				        flex-direction: column;
				-webkit-box-align:center;
				    -ms-flex-align:center;
				        align-items:center;
				-webkit-box-pack: center;
				    -ms-flex-pack: center;
				        justify-content: center;
} 
			/*上边的层*/
#top_div {
                width: 90%;
                height:85px;
                margin-left: 5%;
                margin-right: 5%;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                    -ms-flex-direction: column;
                        flex-direction: column;
				-webkit-box-align:center;
				    -ms-flex-align:center;
				        align-items:center;
				-webkit-box-pack: center;
				    -ms-flex-pack: center;
				        justify-content: center;
} 
			/*上边层里面的设置*/
#top_div div{
				line-height : 60px;
				vertical-align : middle;
				text-align :center;
}		
			/*上边层里面的左翻页的层*/
#top_div #prev_div{
				width : 22%;
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-orient: horizontal;
				-webkit-box-direction: normal;
				    -ms-flex-direction: row;
				        flex-direction: row;
				-webkit-box-align:center;
				    -ms-flex-align:center;
				        align-items:center;
				margin-left: 3%;
				-webkit-box-pack: start;
				    -ms-flex-pack: start;
				        justify-content: flex-start;
}
			/*签到记录*/
.sign-record-div{
				width : 50%;
				height: 45px;
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-orient: horizontal;
				-webkit-box-direction: normal;
				    -ms-flex-direction: row;
				        flex-direction: row;
				-webkit-box-align:center;
				    -ms-flex-align:center;
				        align-items:center;
				-webkit-box-pack: center;
				    -ms-flex-pack: center;
				        justify-content: center;
}	
	
			/*上边层里面的右翻页的层*/
#top_div #next_div{
				width : 22%;
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-orient: horizontal;
				-webkit-box-direction: normal;
				    -ms-flex-direction: row;
				        flex-direction: row;
				-webkit-box-align:center;
				    -ms-flex-align:center;
				        align-items:center;
				margin-right: 3%;
				-webkit-box-pack: end;
				    -ms-flex-pack: end;
				        justify-content: flex-end;
}	
			/*上边层里面的图片设置*/
#top_div img{
				width : 20px;
				height : 20px;
}		
			/*主要显示日历的层*/
#calendar_div {
          		padding-left: 4.5%;
          		padding-right: 4.5%;
          		display: -webkit-box;
          		display: -ms-flexbox;
          		display: flex;
				-webkit-box-orient: horizontal;
				-webkit-box-direction: normal;
				    -ms-flex-direction: row;
				        flex-direction: row;
				-webkit-box-align:center;
				    -ms-flex-align:center;
				        align-items:center;
          		-ms-flex-wrap:wrap;
          		    flex-wrap:wrap
} 
			/*主要显示日历的层里面的层*/
#calendar_div div {
                width: 14.28%;
                height: 40px;
                font-size: 12px;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
				-webkit-box-orient: horizontal;
				-webkit-box-direction: normal;
				    -ms-flex-direction: row;
				        flex-direction: row;
				-webkit-box-align:center;
				    -ms-flex-align:center;
				        align-items:center;
				-webkit-box-pack: center;
				    -ms-flex-pack: center;
				        justify-content: center;
}
#calendar_div span{
            	width: 22px;
            	height: 22px;
            	border-radius: 11px;
            	display: -webkit-box;
            	display: -ms-flexbox;
            	display: flex;
            	position:relative;
				-webkit-box-orient: horizontal;
				-webkit-box-direction: normal;
				    -ms-flex-direction: row;
				        flex-direction: row;
				-webkit-box-align:center;
				    -ms-flex-align:center;
				        align-items:center;
				-webkit-box-pack: center;
				    -ms-flex-pack: center;
				        justify-content: center;
}
.week-text-div{
		width: 90%;
		margin-left: 5%;
		margin-right: 5%;
		height: 40px;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		    -ms-flex-direction: row;
		        flex-direction: row;
		-webkit-box-align:center;
		    -ms-flex-align:center;
		        align-items:center;
		-webkit-box-pack: center;
		    -ms-flex-pack: center;
		        justify-content: center;
}
.weekend-day{
		width: 13%;
		color: #999999;
}
.working-day{
		width: 14.28%;
}
.grey-line-div{
		width: 100%;
		height: 1px;
		background-color: #DDDDDD;
}
.calendar-date-div{
		height: 40px;
		width: 100%;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		    -ms-flex-direction: row;
		        flex-direction: row;
		-webkit-box-align:center;
		    -ms-flex-align:center;
		        align-items:center;
		-webkit-box-pack: center;
		    -ms-flex-pack: center;
		        justify-content: center;
}
		/*上边层里面的年月显示的层*/
.calendar-date-text{
		width : 50%;
		height: 40px;
		font-size:14px ;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		    -ms-flex-direction: row;
		        flex-direction: row;
		-webkit-box-align:center;
		    -ms-flex-align:center;
		        align-items:center;
		-webkit-box-pack: center;
		    -ms-flex-pack: center;
		        justify-content: center;
}



