
.sign-list-root{
	width: 100%;
	height: 60px;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex; 
	-webkit-box-orient: horizontal; 
	-webkit-box-direction: normal; 
	    -ms-flex-direction: row; 
	        flex-direction: row;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.sort-num{
	width: 20px;
	margin-left: 10px;
	margin-right: 0px;
	font: "微软雅黑";
	font-style: italic;
	font-size: 19px;
}
.rank-icon{
	width: 20px;
	height: 20px;
	margin-bottom: 20px;
	margin-right: -10px;
	z-index: 1;
}
.user-icon{
	width: 40px;
	height: 40px;
	border-radius: 15px;
}
.sign-list-item-center{
	width: calc(100% - 190px);
}
.sign-list-item-center{
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex; 
	-webkit-box-orient: vertical; 
	-webkit-box-direction: normal; 
	    -ms-flex-direction: column; 
	        flex-direction: column;
	-webkit-box-align: start;
	    -ms-flex-align: start;
	        align-items: flex-start;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	margin-left: 10px;
}
.user-name{
	width: 100%;
	font-size: 17px;
	overflow: hidden;
	text-overflow:ellipsis;
	white-space:nowrap;
	text-align: start;
}
.sign-line{
	width: 100%;
	height: 4px;
	margin-top: 3px;
	border-radius: 2px;
	background: #00ABFD;
}
.sign-list-item-right-outer{
	width: 100px;
	margin-top: 4px;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex; 
	-webkit-box-orient: horizontal; 
	-webkit-box-direction: normal; 
	    -ms-flex-direction: row; 
	        flex-direction: row;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: end;
	    -ms-flex-pack: end;
	        justify-content: flex-end;
}
.sign-list-item-right{
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex; 
	-webkit-box-orient: vertical; 
	-webkit-box-direction: normal; 
	    -ms-flex-direction: column; 
	        flex-direction: column;
	-webkit-box-align: end;
	    -ms-flex-align: end;
	        align-items: flex-end;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}
.get-points{
	margin-right: 10px;
	padding-left: 4px;
	padding-right: 4px;
	border-radius: 5px;
	border-width: 4px;
	border: 1px solid grey; 
	display: -webkit-box; 
	display: -ms-flexbox; 
	display: flex;
	white-space:pre;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	font-size: 15px;
}
.sign-time{
	margin-right: 15px;
	margin-top: 4px;
	font-size: 13px;
	color: grey;
}



