
.titleBar{
		height: 40px;
		width: 100%;
		background-color: #00abfd;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		    -ms-flex-direction: row;
		        flex-direction: row;
		-webkit-box-align:center;
		    -ms-flex-align:center;
		        align-items:center;
}
.leftDiv{
		width: 20%;
		height: 100%;
		text-align: left;
}
.leftImg{
		height: 24px;
		width: 24px;
		margin-left: 13px;
		margin-top: 8px;
}
.centerDiv{
		width: 60%;
		height: 100%;
		text-align: center;
}
.titleName{
		height: 100%; 
        line-height: 43px;  
		color: white;
		font-size: 20px;
}
.rightDiv{
		width: 20%;
		height: 100%;
		text-align: right;
}
.rightImg{
		height: 22px;
		width: 26px;
		margin-right: 15px;
		margin-top: 9px;
}



