
.check-info-dialog{
	  position: fixed;
	  top: 0;
	  left: 0;
	  width: 100%;
	  height: 100%;
	  background-color: rgba(0, 0, 0, 0.5);
	  display: -webkit-box;
	  display: -ms-flexbox;
	  display: flex;
	  display: -webkit-flex;
	  -webkit-box-pack: center;
	      -ms-flex-pack: center;
	          justify-content: center;
	  -webkit-justify-content: center;
	  -webkit-box-align: center;
	      -ms-flex-align: center;
	          align-items: center;
	  -webkit-align-items: center;
	  z-index: 100;
}
.info-outer-div{
	width: 100%;
	height: 90%;
	background-color: white;
	border-radius: 6px;
}
.info-close-btn{
	width: 98%;
	height: 25px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: end;
	    -ms-flex-pack: end;
	        justify-content: flex-end;
	margin-right: 2%;
	margin-top: 2%;
}
.textcenter{
	text-align: center;
	display:inline-block;
}
.info-title{
	width: 80%;
	margin-left: 10%;
	font-size: 27px;
}
.info-detail-div{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	width: 100%;
	margin-top: 2%;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}
.info-detail{
	font-size: 17px;
	white-space: nowrap;
	margin-right: 15px;
	line-height: 17px;
}
.info-content-div{
	width: 100%;
	margin-left: 5%;
	margin-top: 3%;
}
#newsImg{
	width: 60%;
	height: 180px;
	margin-left: 20%;
}



